* {
  box-sizing: border-box;
}

body {
  background-color: rgba(72, 245, 245, .58);
  font-family: Lato;
}

img {
  max-width: 100%;
  display: block;
}

li {
  list-style: none;
}

a {
  font-size: 40px;
}

